<template>
  <div class="hello">
    <iframe id='iframe' :src="pldurl"></iframe>
  	<div class="pldright">
  		<img class="pldadmin" src="static/img/admin.png" alt="admin"/>
  		<div>
  			<div>
  				<div class="pldaddsb">
  					<button @click="pldadd">添加设备</button>
  				</div>
  				<ul class="pldadd" v-show="pldshow[0].show">
  					<li v-for="(it,ia) in pldlist">
  						{{it.pp}}
  						<input type="text" v-model="it.ipt" placeholder="请输入需要的值"/>
  					</li>
  					<button @click="pldsure">确定</button>
  				</ul>
  				<div class="pldlblist">
  					<table>
  						<thead>
  							<td v-for="(il,idd) in pldsblist">
  								{{il.pp}}
  							</td>
  						</thead>
  						<tbody>
  							<tr v-for="(ii,id) in pldfinval">
  								<td v-if="ii.show">
  									{{ii.pp}}
  								</td>
  								<td v-else-if="ii.show==false">
  									<input type="text" v-model="ii.pldval" placeholder="请输入需要的值"/>
  								</td>
  								<td>
  									{{ii.status}}
  								</td>
  								<td>
  									<button @click="pldtext(id)">
  										{{ii.pp1}}
  									</button>
  									<button @click="suredel(id)">
  										{{ii.pp2}}
  									</button>
  								</td>
  							</tr>
  						</tbody>
  					</table>
  				</div>
  			</div>
  		</div>
  	</div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
    	pldurl:'',
    	//各种dom是否显示
    	pldshow:[
    	//添加设备是否显示
    		{show:false}
    	],
      msg: 'Welcome to Your Vue.js App',
      pldlist:[
      	{pp:'device_eui：',ipt:''},
      	{pp:'name：',ipt:''}
//    	{pp:'icon：'}
      ],
      pldsblist:[
      	{pp:'设备名称'},
      	{pp:'状态'},
      	{pp:'操作'}
      ],
      pldfinval:[
      	{pp:'angus-008',status:'online',show:true,pp1:'编辑',pp2:'删除',dev_eui:'',pldval:''},
      	{pp:'angus-008',status:'online',show:true,pp1:'编辑',pp2:'删除',dev_eui:'',pldval:''},
      	{pp:'angus-008',status:'online',show:true,pp1:'编辑',pp2:'删除',dev_eui:'',pldval:''}
      ]
    }
  },
  methods:{
  	'pldgeturl':function(){
  		let that=this
  		that.$ajax.get(that.pld_global.pldbaseurl+'/mappage',{
  			headers:{
	  			'Authorization':window.localStorage.getItem('token')
	  		}
			})
	  	.then(function(res){
	  		that.pldurl=res.data.mappage
	  	})
	  	.catch(function(dd){
	  		alert(dd)
	  	})
  	},
  	'pldtext':function(dd){
  		let that=this
  		if(this.pldfinval[dd].show){
  			this.pldfinval[dd].show=false
  			this.pldfinval[dd].pp1='确定'
  		}else{
  			this.pldfinval[dd].show=true
  			this.pldfinval[dd].pp1='编辑'
  			that.$ajax.put(that.pld_global.pldbaseurl+'/device',{
					device_eui:that.pldfinval[dd].dev_eui,
					name:that.pldfinval[dd].pldval
  			},{headers:{
	  			'Authorization':window.localStorage.getItem('token')
	  		}})
  			.then(function(){
					alert('修改成功')
  				that.getpldsb()
  				
  			})
  			.catch(function(dd){
  				alert(dd)
  			})
  		}
  	},
  	'pldadd':function(){
  		this.pldshow[0].show=true
  	},
  	'pldsure':function(){
  		let that=this
  		that.$ajax.post(that.pld_global.pldbaseurl+'/device',{
					device_eui:that.pldlist[0].ipt,
					name:that.pldlist[1].ipt
  			},{headers:{
	  			'Authorization':window.localStorage.getItem('token')
	  		}})
  			.then(function(){
					alert('新增成功')
					that.pldshow[0].show=false
  				that.getpldsb()
  				
  			})
  			.catch(function(dd){
  				alert(dd)
  			})
  	},
  	'suredel':function(dd){
  		let that=this
  		let r=confirm('确认要删除吗？')
  		if(r==true){
  			that.$ajax.delete(that.pld_global.pldbaseurl+'/device/'+that.pldfinval[dd].dev_eui,{
  				headers:{
		  			'Authorization':window.localStorage.getItem('token')
		  		},
  			}).then(function(){
					alert('删除成功')
  				that.getpldsb()
  				
  			})
  			.catch(function(dd){
  				alert(dd)
  			})
  		}else{
  			alert('中途取消')
  		}
  	},
  	'getpldsb':function(){
  		let that=this
  		that.$ajax.get(that.pld_global.pldbaseurl+'/device',{
				params:{
					page:1,
					perpage:10
				},
				headers:{
	  			'Authorization':window.localStorage.getItem('token')
	  		}
			})
	  	.then(function(res){
	  		console.log(res)
	  		that.pldfinval.splice(0,that.pldfinval.length)
	  		for(let i=0;i<res.data.result.devices.length;i++){
	  			let pldpp={pp:'',status:'',show:true,pp1:'编辑',pp2:'删除',dev_eui:'',pldval:''}
	  			pldpp.pp=res.data.result.devices[i].device_name
	  			pldpp.status=res.data.result.devices[i].status
	  			pldpp.dev_eui=res.data.result.devices[i].device_eui
	  			that.pldfinval.push(pldpp)
	  		}
	  	})
	  	.catch(function(){
	  		
	  	})
  	}
  },
  mounted:function(){
  	//获取设备列表
  	this.getpldsb()
  	this.pldgeturl()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
#iframe{
	width: 100%;
	height: 100%;
	border: none;
	outline: none;
}
.hello{
	overflow: hidden;
	.pldright{
		table{
			padding: 20px;
			width: 100%;
			thead{
				width: 100%;
				td{
					width: 33%;
				}
			}
		}
		ul{
			overflow: hidden;
			li{
				margin-left: 20px;
				list-style-type: none;
				float: left;
			}
		}
		.pldaddsb{
			button{
				margin-bottom: 20px;
				margin-top: 15px;
			}
		}
		height:25px;
		width: 2%;
    float: right;
    position: absolute;
    top: 32%;
    right: 0px;
    overflow: hidden;
    background-color: #fff;
		.pldadmin{
			position: relative;
			top: 2px;
			width: 20px;
			height: 20px;
			cursor: pointer;
		}
	}
	/*浮动层的动画*/
	@keyframes pldadmin{
		from{width: 2%;}
		to{width: 40%;height: auto;}
	}
	.pldright:hover{
		animation: pldadmin .6s;
		animation-fill-mode: forwards;
	}
}
</style>



