<template>
	<div class="pldlogin">
		<!--<img src="static/img/logo.png" class="pldlogomore" />-->
		<div class="pldlogincontent">
			<!--<img src="static/img/sign_in.png" alt="login" class="loginimg" />-->
			<div class="pldadddiv">
				<p class="pldloginp">
					{{pldloginlist[0].pp}}
				</p>
				<div class="pldpld">
					<p v-show="pldloginlist[1].pldkg">{{pldloginlist[1].pp}}</p>
					<input v-for="(it,ii) in pldloginlist[3]" v-if="ii==0" type="text" v-model="userlist[0].pp" :placeholder="it.pla" />
					<input v-for="(it,ii) in pldloginlist[3]" v-if="ii==1" type="password" v-model="userlist[1].pp" :placeholder="it.pla" />
				</div>
				<!--<div class="pldmorepass" @click="show">
					<div>
						<img src="static/img/password.jpg" v-show="ask" alt="password" />
						<a>{{pldloginlist[2].pp}}</a>
					</div>
				</div>-->
				<div class="pldbtn">
					<button @click="gettoken">
						{{pldloginlist[0].pp}}
					</button>
					<!--<img src="static/img/loginbtn.png" alt="loginbtn" class="loginbtn"/>-->
				</div>
			</div>
		</div>
		<p class="pldfooterp">
			Copyright © 2018 ATENXA. ALL Rights Reserved
		</p>
	</div>
</template>

<script>
	export default{
		name:'login',
		data(){
	 	  	return{
	 	  		ask:true,
	 	  		userlist:[
	 	  			{pp:''},
	 	  			{pp:''}
	 	  		],
	 	  		pldloginlist:[
	 	  			{pp:'登录'},
	 	  			{pp:'账号或密码',pldkg:false},
	 	  			{pp:'记住密码'},
	 	  			[
	 	  				{pla:'请输入账号'},
	 	  				{pla:'请输入密码'}
	 	  			]
	 	  		],
	 	  		pldzz:''
	 	  	}
 	  },
 	  mounted:function(){
 	  	let that=this
 	  	document.onkeydown=function(event){
 	  		var e = event || window.event || arguments.callee.caller.arguments[0];
              if(e && e.keyCode==13){ //  
                  that.gettoken()
                }
 	  	}
//		$(document).keydown(function (event) {
//		    if (event.keyCode == 13) {
//		    	that.gettoken()
//		    };
//		});
		
 	  },
 	  methods:{
  		'show':function(event){
  			if(this.ask==false){
  				this.ask=true
  			}else{
  				this.ask=false
  			}
  		},
  		'gettoken':function(){
  			if(this.userlist[0].pp==''||this.userlist[1].pp==''){
  				this.pldloginlist[1].pp='账号或密码不能为空'
  				this.pldloginlist[1].pldkg=true
  				return
  			}
  			
			this.$ajax.post(this.pld_global.pldbaseurl+'/user/login',{
		      	user_name:this.userlist[0].pp,
				password:this.userlist[1].pp
		    })
		      .then((res)=>{
		      	window.localStorage.setItem('token',res.data.result.jwt)
		      	this.$router.push({path:'/mains'})
		      })
		      .catch((error)=>{
				this.pldloginlist[1].pp='账号或密码错误'
				this.pldloginlist[1].pldkg=true
		      })
  			
  		}
 	  }
	}
</script>

<style lang="less">
	body{
		overflow: hidden;
	}
	.hide,.hidetwo{
		display: none;
	}
	.hidetwo{
		float: none;
	}
	.hidett{
		float: none;
		width: 100%;
	}
	.pldlogin{
		min-width: 1200px;
		background: url(../../static/img/bg.png) no-repeat;
		background-size: 100% 100%;
		position: absolute;
	    width: 100%;
	    height: 100%;
	    overflow: hidden;
	    z-index: 25;
	    background-color: #12d1ed;
	    left: 0;
	    .pldlogomore{
	    	margin-top: 20px;
		    margin-left: 30px;
		    position: absolute;
		    top: 5px;
		    left: 30px;
	    }
		.pldlogincontent{
			position: absolute;
			z-index: 9;
			width: 323px;
			background-color: #fff;
			border-radius: 6px;
			padding: 25px 21px;
			left:56%;
			top:25%;
			.pldadddiv{
				position: relative;
				z-index: 9;
			}
			.pldloginp{
				color: #333;
				font-size: 28px;
			}
			.pldpld{
				margin-top: 13px;
				position: relative;
				input{
					height: 40px;
					width: 94%;
					padding-left: 15px;
					margin-top: 20px;
					border: 1px solid #dbdbdb;
					border-radius: 4px;
					position: relative;
				}
				input::-webkit-input-placeholder{
					color: #dbdbdb;
				}
				p{
					/*display: none;*/
					position: absolute;
					top: -23px;
					color: red;
				}
			}
			.pldmorepass{
				padding-left: 2px;
				margin-top: 20px;
				margin-bottom: 20px;
				position: relative;
				overflow: hidden;
				height: 20px;
				a{
					position: absolute;
					top: 1px;
					color: #999;
					cursor: pointer;
					margin-left: 4px;
					left: 19px;
				}
			}
			.pldmorepass:after{
				content: '';
				display: block;
				width: 16px;
				height: 16px;
				border: 1px solid #c3c2c2;
				position: absolute;
				top: 3px;
				left: 0px;
			}
			.pldbtn{
				button{
					height: 40px;
					width: 100%;
					margin-top: 20px;
					outline: none;
					border: none;
					border-radius: 4px;
					/*background: -webkit-linear-gradient(left, #8652ff, #1bd1f5);
					background: -o-linear-gradient(left, #8652ff, #1bd1f5);
					background: -moz-linear-gradient(left, #8652ff, #1bd1f5);*/
					background: -webkit-linear-gradient(left, #8652ff, #1bd1f5);
					color: #e1f7d9;
					position: relative;
					z-index: 10;
					border-bottom: 1px solid rgba(255,255,255,0.6);
				}
			}
			.loginbtn{
				position: absolute;
			    bottom: -14px;
			    left: -15px;
			    bottom: -26px;
			    z-index: 1;
			    opacity: 0.8;
			}
		}
		.loginimg{
			position: absolute;
		    top: -7%;
    		right: -6%;
		    width: 830px;
		}
		.rightall{
			font-size: 16px;
			position: relative;
			top: 16px;
			margin-right: 3%;
			color: #666666;
			float: right;
			select{
				background-color: transparent;
				color: #f3fdf2;
				border: none;
				outline: none;
				option{
					background-color: #23c5f2;
				}
			}
		}
		.pldfooterp{
			height: 40px;
			background-color: #2c2a38;
			line-height: 40px;
			text-align: center;
			color: #8d8e8d;
			position: absolute;
			bottom: -10px;
			width: 100%;
			/*padding-left: 380px;*/
		}
	}
</style>


